.wrapper {
  box-shadow: 0px 5px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}
.menu {
  display: flex;
  margin-top: 20px;
  margin-bottom: 50px;
}
.link {
  display: inline-block;
  margin-left: 20px;
  font-size: 20px;
  font-weight: bold;
  color: black;
  text-decoration: none;
}
.link.active {
  color: red;
}
